// Media queries for mobile content (portrait)
@media screen and (min-width: 250px) and (max-width: 650px) and (min-height: 950px),
screen and (min-width: 300px) and (max-width: 650px) and (min-height: 550px),
screen and (min-width: 500px) and (max-width: 550px) and (min-height: 500px) and (max-height: 550px) {

    // Dark mode content
    .dark_mode_container {
        top: .75%;
        right: 2%;
        width: 1.1rem;
        height: 1.1rem;

        &:hover {
            background-color: var(--dark-mode-container-bg-color);

            i {
                color: var(--dark-mode-icon-color);
            }
        }

        i {
            font-size: .685rem;
        }
    }
}


// Media queries for small mobile content (portrait)
@media screen and (min-width: 250px) and (max-width: 400px) and (min-height: 350px) and (max-height: 400px), 
screen and (min-width: 250px) and (max-width: 500px) and (min-height: 400px) and (max-height: 550px), 
screen and (min-width: 250px) and (max-width: 300px) and (min-height: 350px) and (max-height: 950px) {

    // Dark mode content
    .dark_mode_container {
        top: .75%;
        right: 1%;
        width: .75rem;
        height: .75rem;

        &:hover {
            background-color: var(--dark-mode-container-bg-color);

            i {
                color: var(--dark-mode-icon-color);
            }
        }

        i {
            font-size: .515rem;
        }
    }
}


// Media queries for mobile content (landscape)
@media screen and (min-height: 300px) and (max-height: 500px) and (min-width: 501px),
screen and (min-height: 500px) and (max-height: 550px) and (min-width: 550px), 
screen and (min-height: 550px) and (max-height: 650px) and (min-width: 650px) {

    // Dark mode content
    .dark_mode_container {
        top: .75%;
        right: .5%;
        width: .925rem;
        height: .925rem;

        &:hover {
            background-color: var(--dark-mode-container-bg-color);

            i {
                color: var(--dark-mode-icon-color);
            }
        }

        i {
            font-size: .615rem;
        }
    }
}


// Media queries for mobile content (landscape low height)
@media screen and (min-height: 250px) and (max-height: 350px) and (min-width: 350px) and (max-width: 500px),
screen and (min-height: 350px) and (max-height: 400px) and (min-width: 400px) and (max-width: 500px),
screen and (min-height: 250px) and (max-height: 300px) and (min-width: 500px) {

    // Dark mode content
    .dark_mode_container {
        top: .75%;
        right: .5%;
        width: .725rem;
        height: .725rem;
        padding: 0;

        &:hover {
            background-color: var(--dark-mode-container-bg-color);

            i {
                color: var(--dark-mode-icon-color);
            }
        }

        i {
            font-size: .45rem;
        }
    }
}


// Media queries for tablet content (portrait)
@media screen and (min-height: 650px) and (min-width: 650px) and (max-width: 800px),
screen and (min-height: 900px) and (min-width: 650px) and (max-width: 900px) {

    // Dark mode content
    .dark_mode_container {
        top: .75%;
        right: 2%;
        width: 1.35rem;
        height: 1.35rem;

        &:hover {
            background-color: var(--dark-mode-container-bg-color);

            i {
                color: var(--dark-mode-icon-color);
            }
        }

        i {
            font-size: .825rem;
        }
    }
}


// Media queries for normal low height wide screens, normal high height medium width screens
@media screen and (min-height: 650px) and (max-height: 750px) and (min-width: 800px),
screen and (min-height: 750px) and (max-height: 900px) and (min-width: 800px) and (max-width: 1200px),
screen and (min-height: 900px) and (min-width: 900px) and (max-width: 1200px) {

    // Dark mode content
    .dark_mode_container {
        top: 1.25%;
        right: 1%;
        width: 1.8rem;
        height: 1.8rem;
        padding: .3rem;

        i {
            font-size: 1.15rem;
        }
    }
}
